arge\']; // B1 = fire mother\n\n    // american dad\n    fotoData[B3] = [\'/b3-francine.webp\', \'Francine\']; // B3 = air mother\n\n    // family guy\n    fotoData[D3] = [\'/d3-peter.webp\', \'Peter Griffin\']; // D3 = air father\n    fotoData[B4] = [\'/b4-lois.webp\', \'Lois Griffin\']; // B4 = water mother\n    fotoData[C2] = [\'/c2-meg-griffin.webp\', \'Meg Griffin\']; // C2 = earth daughter\n\n    // disenchantment\n    fotoData[C1] = [\'/c1-bean.webp\', \'Bean\']; // C1 = fire daughter\n    fotoData[D4] = [\'/d4-odval.webp\', \'Odval\']; // D4 = water father\n\n\n\n    // inline images to javascript\n    // TODO move to end of script for faster page load\n\n    const fs = require(\'fs\');\n    const mime = require(\'mime-types\');\n\n    return fotoData.map(([file, name]) => {\n\n      // options.baseDir is defined in rollup.config.js\n      const file_abs = options.baseDir + \'/src/images\' + file;\n\n      if (!fs.existsSync(file_abs)) {\n        console.log(\'error: no such file: \'+file_abs);\n        console.log(\'baseDir: \'+options.baseDir);\n        return [file, name];\n      }\n\n      const file_type = mime.lookup(file_abs)\n        || \'application/octet-stream\';\n\n      const base64data = fs.readFileSync(\n        file_abs, {encoding: \'base64\'}\n      );\n\n      const data_uri = \'data:\'+file_type+\';base64,\'+base64data;\n\n      //console.log(\'good: inlining file: \'+file_abs+\' as \'+data_uri.substring(0,100)+\' ....\');\n\n      return [data_uri, name];\n\n    });\n\n  });\n\n  window.sourceCode = preval(\n    ({baseDir}) => {\n      const sourceFileĹist = [\n        \'src/AlchiMaps.svelte\',\n        \'src/main.js\',\n        \'src/alchi.js\',\n        \'src/colorTable.cjs\',\n        \'rollup.config.js\',\n        \'package.json\',\n        \'README.md\',\n        \'LICENSE\',\n        \'public/alchi-maps.html\',\n        \'public/index.html\',\n      ];\n      const fs = require(\'fs\');\n      return sourceFileĹist.reduce(\n        (acc, sourceFile) => {\n          acc[sourceFile] = fs.readFileSync(\n            baseDir+\'/\'+sourceFile, {\n            encoding: \'utf-8\',\n          });\n          return acc;\n        },\n        {}\n      );\n    }\n  );\n\n<\/script>\n\n\x3c!--\n  on:keydown={handleKeydown}\n--\x3e\n<svelte:window\n  on:load={handleWindowLoad}\n/>\n\n\x3c!-- set css variables --\x3e\n\x3c!-- TODO instead of $colors2dyn, use CSS transition for colors --\x3e\n<main style="\n    --fg: {$colors2dyn[0]};\n\n    --bg: {$colors2dyn[1]};\n    --bg-alpha33: rgba({col_rgba33[1]});\n  ">\x3c!--<div class="scroll">--\x3e<div>\n\n  \x3c!-- define svg symbols --\x3e\n  <svg style="display: none">\n\n    <symbol id="element-and-pathos" viewBox="0 0 400 300">\n      \x3c!--\n      <circle cx="50" cy="100" r="40" style="stroke: var(--fg)" />\n      <rect x="50" y="150" width="20" height="20" style="stroke: var(--fg)" />\n      --\x3e\n\n      \x3c!-- svg path arc commands:\n      A rx ry x-axis-rotation large-arc-flag sweep-flag x y\n      a rx ry x-axis-rotation large-arc-flag sweep-flag dx dy\n      --\x3e\n\n      \x3c!--\n      <g style="stroke: var(--fg); fill: none" >\n        {#each Array.from(Array(10).keys()) as idx}\n          <line x1="{idx*50}" x2="{idx*50}" y1="0" y2="300" />\n        {/each}\n        {#each Array.from(Array(10).keys()) as idx}\n          <line x1="0" x2="400" y1="{idx*50}" y2="{idx*50}" />\n        {/each}\n      </g>\n      --\x3e\n\n      <path d="\n        M 10 150 \n        a 20 20 0 0 1 40 0 \n        a 20 20 0 0 1 40 0\n        a 20 20 0 0 1 -20 20\n        a 20 20 0 0 0 -20 20\n        a 20 20 0 0 0 -20 -20\n        a 20 20 0 0 1 -20 -20\n        z\n      " style="\n        stroke: var(--fg); fill: red"/>\n\n\n      <path d="\n        M 10 50 \n        a 20 20 0 0 1 40 0 \n        a 20 20 0 0 1 40 0\n        a 20 20 0 0 1 -40 0\n        a 20 20 0 0 1 -40 0\n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n      <path d="\n        M 10 250 \n        a 20 20 0 0 1 20 -20 \n        a 20 20 0 0 0 20 -20 \n        a 20 20 0 0 0 20 20 \n        a 20 20 0 0 1 20 20 \n        a 20 20 0 0 1 -20 20 \n        a 20 20 0 0 0 -20 20 \n        a 20 20 0 0 0 -20 -20 \n        a 20 20 0 0 1 -20 -20 \n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n      <path d="\n        M 110 150 \n        a 20 20 0 0 1 20 -20 \n        a 20 20 0 0 0 20 -20 \n        a 20 20 0 0 0 20 20 \n        a 20 20 0 1 1 -20 20 \n        a 20 20 0 0 1 -40 0 \n        z\n      " style="\n        stroke: var(--fg); fill: green"/>\n\n      <path d="\n        M 110 50 \n        a 20 20 0 0 1 40 0 \n        a 20 20 0 0 1 40 0\n        a 20 20 0 0 1 -40 0\n        a 20 20 0 0 1 -40 0\n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n      <path d="\n        M 110 250 \n        a 20 20 0 0 1 20 -20 \n        a 20 20 0 0 0 20 -20 \n        a 20 20 0 0 0 20 20 \n        a 20 20 0 0 1 20 20 \n        a 20 20 0 0 1 -20 20 \n        a 20 20 0 0 0 -20 20 \n        a 20 20 0 0 0 -20 -20 \n        a 20 20 0 0 1 -20 -20 \n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n      <path d="\n        M 210 150 \n        a 20 20 0 0 1 40 0 \n        a 20 20 0 0 1 40 0\n        a 20 20 0 0 1 -40 0\n        a 20 20 0 0 1 -40 0\n        z\n      " style="\n        stroke: var(--fg); fill: yellow"/>\n\n      <path d="\n        M 210 50 \n        a 20 20 0 0 1 20 -20 \n        a 20 20 0 0 0 20 -20 \n        a 20 20 0 0 0 20 20 \n        a 20 20 0 1 1 -20 20 \n        a 20 20 0 0 1 -40 0 \n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n      <path d="\n        M 210 250 \n        a 20 20 0 0 1 40 0 \n        a 20 20 0 0 1 40 0\n        a 20 20 0 0 1 -20 20\n        a 20 20 0 0 0 -20 20\n        a 20 20 0 0 0 -20 -20\n        a 20 20 0 0 1 -20 -20\n        z\n      " style="\n        stroke: var(--fg); fill: none"/>\n\n\n      <path d="\n        M 310 150 \n        a 20