reak: break-all;\n                            width: 160px; height: 160px;\n\n                            text-align: center;\n                            display: flex;\n                            flex-direction: column;\n                          "\n                        >\n                          <div\n                            style="\n                              margin-top: 10px;\n                              height: 70px; width: 160px;\n                              display: flex;\n                              justify-content: center;\n                              align-items: center;\n                              color: {valColor.sm[sm][1]};\n                            "\n                          >{nameFromN[num][0][1]}</div>\n                          \n                          <div\n                            style="\n                              height: 70px; width: 160px;\n                              display: flex;\n                              justify-content: center;\n                              align-items: center;\n                              color: {valColor.ag[ag][1]};\n                        "\n                          >{nameFromN[num][1][1]}</div>\n                        \n                        </div>\n\n                      </foreignObject>\n\n\n\n                    {:else if nameParts === 2 && nameFormat === \'be\'}\n\n                      <foreignObject\n                        x="20" y="20"\n                        width="160" height="160"\n                      >\n\n                        <div\n                          xmlns="http://www.w3.org/1999/xhtml"\n                          style="\n                            line-height: 1em;\n                            font-family: sans-serif;\n\n                            font-size: 54px;\n\n                            padding: 0; margin: 0;\n\n                            word-break: break-all;\n                            width: 160px; height: 160px;\n\n                            text-align: center;\n                            display: flex;\n                            flex-wrap: wrap;\n                            flex-direction: column;\n                          "\n                        >\n                          \n                          <div\n                            style="\n                              height: 160px; width: 80px;\n                              display: flex;\n                              justify-content: center;\n                              align-items: center;\n                              color: {valColor.ag[ag][1]};\n                            "\n                            title="{nameFromN[num][0][1]}"\n                          >{nameFromN[num][0][0]}</div>\n                          \n                          <div\n                            style="\n                              height: 160px; width: 80px;\n                              display: flex;\n                              justify-content: center;\n                              align-items: center;\n                              color: {valColor.sm[sm][1]};\n                            "\n                            title="{nameFromN[num][1][1]}"\n                            >{nameFromN[num][1][0]}</div>\n\n                        </div>\n\n                      </foreignObject>\n\n\n\n                    {:else if nameParts === 2 && nameFormat === \'e/bxfoto\'}\n\n                      <foreignObject\n                        x="20" y="20"\n                        width="160" height="160"\n                      >\n\n                        <div\n                          xmlns="http://www.w3.org/1999/xhtml"\n                          style="\n                            line-height: 1em;\n                            font-family: sans-serif;\n\n                            font-size: 27px;\n\n                            padding: 0; margin: 0;\n\n                            word-break: break-all;\n                            width: 160px; height: 160px;\n\n                            text-align: center;\n                            display: flex;\n                            flex-wrap: wrap;\n                            flex-direction: row;\n                          "\n                        >\n                          \n                          <div\n                            class="foto-container-high"\n                            style="\n                              height: 160px; width: 122px;\n                              display: flex;\n                              justify-content: center;\n                              align-items: center;\n                            "\n                            on:click="{() => editFoto(num)}"\n                            \n                          ><img class="foto" src="{fotoData[num][0]}" alt="foto of {nameFromN[num][0][1]} {nameFromN[num][1][1]}" title="{fotoData[num][1]}" /></div>\n\n                          <div\n                            style="\n                              height: 160px; width: 38px;\n                              display: flex;\n                              flex-wrap: wrap;\n                              flex-direction: column;\n                            "\n                          >\n\n                            <div\n                              style="\n                                height: 80px; width: 38px;\n                                display: flex;\n                                justify-content: center;\n                                align-items: center;\n                                color: {valColor.ag[ag][1]};\n                              "\n                              title="{nameFromN[num][1][1]}"\n                              >{nameFromN[num][1][0]}</div>\n\n                            <div\n                              style="\n                                height: 80px; width: 40px;\n                                display: flex;\n                   