 {/each}\n      {/each}\n    </g>\x3c!-- close #grid --\x3e\n  </g>\x3c!-- close #grid_wrap --\x3e\n\n</svg>\x3c!-- close #content --\x3e\n\n</div>\x3c!-- close #transform-translate --\x3e\n</div>\x3c!-- close #transform-rotate --\x3e\n</div>\x3c!-- close #transform-down --\x3e\n</div>\x3c!-- close #transform-move --\x3e\n\n<div id="control-wrapper"\n\n  style="\n    width: {width}px; height: {height}px;\n    margin-top: {-2*height -4.5}px;/* TODO why -4? */\n    \n    line-height: 0; /* remove vertical spacing */\n\n    transform: translateZ(1px);\n  "\n>\n\n{#if true}\n\n  {#each [\n\n    // frame width = 1/4\n    [\'top-left\', \'none\', 0, 0, width/4, height/4],\n    [\'top-center\', \'none\', width/4, 0, width/4*2, height/4],\n    [\'top-right\', \'none\', width/4*3, 0, width/4, height/4],\n\n    [\'center-left\', \'none\', 0, height/4, width/4, height/4*2],\n    [\'center-center\', \'move\', width/4, height/4, width/4*2, height/4*2],\n    [\'center-right\', \'none\', width/4*3, height/4, width/4, height/4*2],\n\n    [\'bottom-left\', \'none\', 0, height/4*3, width/4, height/4],\n    [\'bottom-center\', \'none\', width/4, height/4*3, width/4*2, height/4],\n    [\'bottom-right\', \'none\', width/4*3, height/4*3, width/4, height/4],\n\n  ] as [name, touchAction, x, y, w, h]}\n\n    \x3c!-- control targets --\x3e\n    <div\n      id="control-{name}"\n      class="control-target"\n      style="\n        display: inline-block;\n\n        background: transparent;\n        touch-action: none;\n\n        width: {w -2}px;\n        height: {h -2}px;\n        \n        padding: 0; margin: 0;\n        font-size: 0;\n        line-height: 0;\n      "\n    ></div>\n\n  {/each}\n\n{/if}\n</div>\x3c!-- close #control-wrapper --\x3e\n\n</div>\x3c!-- close #transform-mask --\x3e\n</div>\x3c!-- close #wrapper --\x3e\n\n<div\n  class="overlay-box"\n  class:hidden="{exportOverlayHide}"\n>\n  <div class="title">export SVG image</div>\n  <div class="close" on:click="{()=>{exportOverlayHide=true;}}">✕</div>\n  <div class="content">\n    \x3c!-- svelte will complain: <a href="javascript: null" id="export-link">right click and save link</a>--\x3e\n    <a href="empty.html" id="export-link">right click and save link</a>\n  </div>\n</div>\n\n</main>\n\n\n\n<style>\n\n  .overlay-box.hidden {\n    display: none;\n  }\n  .overlay-box {\n    z-index: +2;\n    padding: 0.25em;\n    /*transform: translateZ(+2px);*/\n    position: absolute;\n    top: 50%; left: 50%;\n    width: 12em; height: 6em;\n    margin: -3em 0 0 -6em;\n    border: solid 1px var(--fg);\n    background: var(--bg);\n  }\n  .overlay-box .title {\n    width: 10.5em; height: 1em;\n    line-height: 0.9em;\n    border-bottom: solid 1px var(--fg);\n    float: left;\n    padding: 0.25em;\n    margin: -0.25em 0 0.25em -0.25em;\n  }\n  .overlay-box .close {\n    cursor: pointer;\n    width: 1em; height: 1em;\n    padding: 0.25em;\n    line-height: 0.9em;\n    border: solid 1px var(--fg);\n    margin-left: -1px;\n    margin: -0.25em -0.25em 0.25em -1px;\n    border-top: none;\n    border-right: none;\n    float: right;\n  }\n  .overlay-box .content {\n    clear: both;\n    height: 4em;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n  }\n\n  a {\n    color: var(--fg);\n    border-bottom: dotted 1px var(--fg);\n  }\n  a:hover {\n    text-decoration: none;\n    border-bottom: solid 1px var(--fg);\n  }\n\n\n\n\n\n\n  tr.empty, tr.empty td {\n    height: 0;\n    padding: 0;\n    margin: 0;\n  }\n\n\n\n  /* css variables. init here + change later in javascript */\n  \n  :root {\n    --bg: #000000;\n    --fg: #ffffff;\n    --bg-alpha33: rgba(0,0,0, 0.33);\n    --angle: 0deg;\n    --angleBack: 0deg;\n  }\n\n\n\n  html, body {\n    margin: 0;\n    padding: 0;\n  }\n\n  main {\n    background-color: var(--bg);\n    color: var(--fg);\n\n    min-height: 100%;\n  }\n\n  p {\n    margin-bottom: 2em;\n  }\n\n  main, p, tt {\n    font-size: 16px;\n  }\n\n  .uppercase, .uppercase * {\n    text-transform: uppercase;\n  }\n\n\n\n  :global(.animate_dash .anim_dash1),\n  :global(.animate_dash .anim_dash2) {\n    stroke-dasharray: 30, 10;\n  }\n\n  /* this needs way too much CPU power [70% cpu]\n    cos of buggy svg implementations\n    workaround:\n    use javascript with a low frame-rate\n\n    .animate .anim_dash1,\n    .animate .anim_dash2 {\n      animation: anim_dash2 4s linear infinite;\n    }\n    @keyframes anim_dash1 {\n      100% { stroke-dashoffset: -80 }\n    }\n    @keyframes anim_dash2 {\n      100% { stroke-dashoffset:  80 }\n    }\n  */\n\n  /* expand = collapsed by default */\n  .expand {\n    /* get compositor layer for GPU animation */\n    /* will-change: transform; */\n    margin-bottom: 10px;\n  }\n\n  .expand .body{\n    /*transition: height .5s ease-in-out;*/\n    transition: height .5s cubic-bezier(0.445, 0.050, 0.550, 0.950 );\n    /* = penner equation for easeInOutSine */\n    height: 0; /* start value */\n    overflow: auto; /* add scrollbar */\n  }\n\n  /* collapse = expanded by default */\n  .collapse .body{\n    /*transition: height .5s ease-in-out;*/\n    transition: height .5s cubic-bezier(0.445, 0.050, 0.550, 0.950 );\n    /* = penner equation for easeInOutSine */\n    /* height: 0; /* start value */\n    /* overflow: auto; /* add scrollbar */\n  }\n\n  /* TODO rename to body-translate */\n  :global(.animate_moves g.body_wrapper),\n\n  /* TODO rename to body-rotate */\n  :global(.animate_moves g.rotateBack),\n  :global(.animate_moves svg.body > g),\n\n  /* TODO rename to group-translate, group-rotate */\n  :global(.animate_moves #transform-translate),\n  :global(.animate_moves #transform-rotate)\n  {\n    /*transition: transform .5s cubic-bezier(0.445, 0.050, 0.550, 0.950 );*/\n    transition: transform .5s linear;\n  }\n\n\n\n  /* scrollbars */\n\n  .scroll::-webkit-scrollbar {\n      width: 20px;\n      height: 20px;\n  }\n\n  .body.scroll::-webkit-scrollbar-track,\n  .body.scroll::-webkit-scrollbar-thumb{\n    border-left: none; /* avoid double borders */\n  }\n\n  .scroll::-webkit-scrollbar-track {\n    /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); */\n    border: solid 1px var(--fg);\n    background: var(--bg);\n  }\n\n  .scroll::-webkit-scrollbar-thumb {\n    outline: 1px solid slategrey;\n    border: solid 1px var(--fg);\n    background: center no-repeat url(\'data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" x="0px" y="0px" height="24px" width="24px"><g style="stroke: %23ffffff" stroke-width="1"><line x1="4" y1="8" x2="20" y2="8" /><line x1="4" y1="16" x2="20" y2="16" /></g></svg>\')\n  }\n\n\n\n  /* expand + collapse */\n\n  .expand .head {\n    padding: 0;\n  }\n\n  .expand .head * {\n    margin: 0;\n  }\n\n  .expand .head button {\n    width: 20px;\n    border-left: none !important;\n    padding: 2px;\n  }\n\n  .expand .head input {\n    width: 120px;\n    padding: 2px 4px;\n  }\n\n  .expand .body button {\n    margin: 0;\n    width: 120px;\n    padding: 2px 4px;\n    text-align: left;\n    vertical-align: center;\n    border-top: none;\n    display: block;\n  }\n\n  .expand--expanded, .expand--collapsed {\n    animation-name: a_expand;\n    animation-duration: 0.5s;\n    animation-timing-function: linear;\n  }\n\n\n\n  /* control menu */\n\n  #control0 {\n    /*\n    position: fixed;\n    */\n    position: absolute;\n    top: 0%; left: 0%;\n    \n    /* TODO android workaround for position:fixed\n      with javascript scroll+zoom handler? */\n\n  }\n\n  .control {\n    /*\n    position: fixed;\n    */\n    z-index: +1;\n\n    /* TODO FIXME scroll broken after expand */\n\n    background-color: var(--bg-alpha33);\n\n    white-space: nowrap;\n\n    width: 160px;\n    overflow-x: hidden;\n\n    /* scroll vertical */\n    max-height: 100%;\n    overflow-y: auto;\n\n    padding: 2px;\n\n    /* TODO show control menu always next to grid, use css "flex" layout? */\n    \n    /* TODO set height to auto? max-height: 100%, overflow auto */\n\n    transform-origin: top left;\n\n    transition:\n      width .5s cubic-bezier(0.445, 0.050, 0.550, 0.950 )\n    ;\n    /* = penner equation for easeInOutSine */\n\n  }\n\n  .control.collapsed > *:not(.nohide) {\n    display: none;\n  }\n\n  .control.collapsed > .nohide button:not(.nohide) {\n    display: none;\n  }\n\n  /* FIXME buttons without svg look odd */\n  .control button.square {\n    padding: 2px; margin: 2px;\n    width: 30px; height: 30px;\n    overflow: hidden;\n  }\n\n  .control button.square > svg {\n    width: 20px; height: 20px;\n    margin: 0;\n  }\n\n  .control div.square {\n    margin: 2px;\n    width: 30px; height: 22px;\n    display: inline-block;\n  }\n\n  .control button, .control input {\n    background-color: var(--bg);\n    border: solid 1px  var(--fg);\n    color: var(--fg);\n  }\n\n  .control {\n    font-size: 16px;\n    text-align: left;\n  }\n\n  main > div {\n    text-align: center;\n    margin: 0;\n    top: 0; left: 0;\n    overflow: auto;\n  }\n\n  svg.body.center circle {\n    stroke-width: 12px;\n    stroke: url(#circle_double_stroke);\n  }\n\n  .control .first-row {\n    margin-top: 16px\n  }\n\n  .control .last-row {\n    margin-bottom: 16px\n  }\n\n  symbol.zodiac {\n    fill: none;\n    stroke-width: 12;\n  }\n\n  div#description {\n    /** / text-align: left; /**/\n    margin: 1em;\n  }\n\n  .foto-container-broad .foto {\n    /* size depends on scale factor html vs svg [TODO verify] */\n    max-height: 105px;\n    max-width: 145px;\n  }\n\n  .foto-container-high .foto {\n    /* size depends on scale factor html vs svg [TODO verify] */\n    height: 157px;\n    width: 119px;\n  }\n\n</style>\n',"src/main.js":"import AlchiMaps from './AlchiMaps.svelte'\n\nconst app = new AlchiMaps({\n  target: document.body,\n\t//props: {\n\t//\tname: 0b0011\n\t//}\n})\n\nexport default app\n","src/alchi.js":'\n// TITLE perfekte sitzordnung, vision vom paradies auf erden\n// TITLE seating-arrangements matchmaking compatibility\n\n// age sense move gen, origin 11\n// signs: 0 = same, 1 = diff\nexport const ac_bits_asmg_11 = [\n    0b1001, 0b0111, 0b1011, 0b0101,\n    0b1110, 0b0000, 0b1100, 0b0010,\n    0b1101, 0b0011, 0b1111, 0b0001,\n    0b1010, 0b0100, 0b1000, 0b0110,\n]\nexport const get_bit = function (num, idx) {\n    return (num & (1 << idx)) >> idx;\n    // TODO benchmark - just for fun\n    // parseInt(num.toString(2).padStart(4,0)[idx],10)\n}\nexport const decode_bitstring = function (asmg) {\n    return parseInt(asmg, 2);\n}\nexport const ac_xor = function(factor, list) {\n    return list.map(function (n) {\n        return factor ^ n\n    })\n}\n// letters of the four dimensions\nexport const letter_asmg = [\n    [\'S\', \'L\'], // age   = Short  or Long\n    [\'E\', \'I\'], // sense = Extra  or Intro\n    [\'N\', \'P\'], // move  = Neuro  or Psycho\n    [\'F\', \'M\'], // gen   = Female or Male\n]\n\n\n\n// element = sense + move\nexport const element = [\n    [\n      \'3\', // _00_ = EN = 3\n      \'1\', // _01_ = EP = 1\n    ], [\n      \'2\', // _10_ = IN = 2\n      \'4\', // _11_ = IP = 4\n    ],\n]\n\n\nexport const element_outside = [\n    [\n      \'C\', // 0__0 = SF = C\n      \'A\', // 0__1 = SM = A\n    ], [\n      \'B\', // 1__0 = LF = B\n      \'D\', // 1__1 = LM = D\n    ],\n]\n\n\n\n// moved to colorTable.js\n\n// TODO add front/back colors\n// light colors: yellow + green\n// dark  colors: blue + red\n// TODO auto-generate color table?\n\nexport const group_color = {\n  2: [\n    0x000000, // black\n    0xffffff, // white\n  ],\n  4: [\n    // quickfix element color\n    // "problem": no more rainbow color scale\n    //   = no mix-colors between red/green and blue/yellow\n    //   = opposite colors not easy to find for text vs back color\n              // color    E   SM   SM\n    0xffff00, // yellow   3   EN   00\n    0xff0000, // red      1   EP   01\n    0x00ff00, // green    2   IN   10\n    0x0000ff, // blue     4   IP   11\n/*\n    0xffff00, // yellow   3   EN   00\n    0xff0000, // red      1   EP   01\n    0x00ff00, // green    2   IN   10\n    0x0000ff, // blue     4   IP   11\n*/\n    /* rainbow color scale\n    0xff0000, // red\n    0xffff00, // yellow\n    0x00ff00, // green\n    0x0000ff, // blue\n    */\n  ],\n  8: [\n    // rainbow color scale\n    0xff0000, // red\n    0xff8000, //   orange\n    0xffff00, // yellow\n    0x80ff00, //   lime\n    0x00ff00, // green\n    0x00ffff, //   turc\n    0x0000ff, // blue\n    0xff00ff, //   purp\n  ],\n  16: [\n    // rainbow color scale\n    0xff0000, // red\n    0xff4000, //     red-orange\n    0xff8000, //   orange\n    0xffc000, //     orange-yellow\n    \n    0xffff00, // yellow\n    0xc0ff00, //     yellow-lime\n    0x80ff00, //   lime\n    0xc0ff00, //     lime-green\n    \n    0x00ff00, // green\n    0x00ff80, //     green-turc\n    0x00ffff, //   turc\n    0x0080ff, //     turc-blue\n    \n    0x0000ff, // blue\n    0x8000ff, //     blue-purp\n    0xff00ff, //   purp\n    0xff0080, //     purp-red\n  ],\n}\n\n\n\nexport const group_color_html = ( function(group_color) {\n  return Object.entries(group_color).reduce(\n    (acc, [numGroups, colors]) =>\n    {\n      acc[numGroups] = colors.map((col) => {\n        return \'#\'+col.toString(16).padStart(6, \'0\')\n      })\n      return acc\n    }, {})\n} )(group_color)\n\n\n\nexport const idxFromDim = {\n  \'A\': 0,\n  \'S\': 1,\n  \'M\': 2,\n  \'G\': 3,\n\n  \'a\': 0,\n  \'s\': 1,\n  \'m\': 2,\n  \'g\': 3,}\n\n\nfunction getDimBit (asmg, dim) {\n  //global idxFromDim\n  return asmg[idxFromDim[dim]]\n}\n\nfunction nameFromGroup (g) {\n  // GxMSxA <-- GM+SA\n  // GxMxSxA <-- GMSA\n  // GMSA <-- G+M+S+A\n  return g.split(\'\').join(\'x\').replace(\'x+x\', \'\')\n}\n\nfunction groupFromName (n) {\n  // GM+SA <-- GxMSxA\n  // G+M+S+A <-- GMSA\n  // GMSA <-- GxMxSxA\n  return n.split(\'\').join(\'+\').replace(\'+x+\', \'\')\n}\n\nexport const letterFromASMG = function (dim, asmg) { //TODO change to (asmg, dim)\n  // ASMG\n  const i = idxFromDim[dim]\n  //console.log(\'dim \'+dim+\'   idx \'+i)\n  if (i !== undefined) {\n    return letter_asmg[i][asmg[i]]\n  }\n  // element\n  if (dim == \'E\' || dim == \'e\') {\n    return element[asmg[1]][asmg[2]]\n  }\n  // outside element = Body = material = family role\n  if (dim == \'B\' || dim == \'b\') {\n    return element_outside[asmg[0]][asmg[3]]\n  }\n}\n\n\n\n// pathos: [same-move pathos, same-sense pathos]\n// pathos == the TWO next-plus-one types\n// one pathos type is horizontal next-plus-one\n// other pathos type is vertical next-plus-one\nexport const N_pathosFromN = [\n  [ 4,  2], [ 5,  3], [ 6,  0], [ 7,  1], // FNES, MNES, FPES, MPES,\n  [ 0,  6], [ 1,  7], [ 2,  4], [ 3,  5], // FNIS, MNIS, FPIS, MPIS,\n  [12, 10], [13, 11], [14,  8], [15,  9], // FNEL, MNEL, FPEL, MPEL,\n  [ 8, 14], [ 9, 15], [10, 12], [11, 13], // FNIL, MNIL, FPIL, MPIL\n]\n\n// zodiac\n// 12 zodiac signs = 4 elements x 3 modalities\n// modality "mutable" = 4 elements\n// modality "fixed" = 4 pathos with psychotic move\n// modality "cardinal" = 4 pathos with neurotic move\n// [how to diff the two pathos types? here: by move]\n// four elements = four "mutable" signs\n// idx 0 1 2 3 = elm 3 1 2 4\n\n/*\nexport const zodiacMutableFromSM = [\n  [\'♊︎\', \'Gemini = mutable air\'],\n  [\'♐︎\', \'Sagittarius = mutable fire\'],\n  [\'♍︎\', \'Virgo = mutable earth\'],\n  [\'♓︎\', \'Pisces = mutable water\'],\n]\nexport const zodiacCardinalFromSM = [\n  [\'♎︎\', \'Libra = cardinal air\'],\n  [\'♈︎\', \'Aries = cardinal fire\'],\n  [\'♑︎\', \'Capricorn = cardinal earth\'],\n  [\'♋︎\', \'Cancer = cardinal water\'],\n]\nexport const zodiacFixedFromSM = [\n  [\'♒︎\', \'Aquarius = fixed air\'],\n  [\'♌︎\', \'Leo = fixed fire\'],\n  [\'♉︎\', \'Taurus = fixed earth\'],\n  [\'♏︎\', \'Scorpius = fixed water\'],\n]\n*/\n\n\n\n\n/*\nhttps://www.cosmopolitan.com/uk/entertainment/horoscopes-monthly/a29383721/star-sign-dates/\n\n1 Aries dates: March 21 – April 19\n2 Taurus dates: April 20 – May 20\n3 Gemini dates: May 21 – June 20\n4 Cancer dates: June 21 – July 22\n\n1 Leo dates: July 23 – August 22\n2 Virgo dates: August 23 – September 22\n3 Libra dates: September 23 – October 22\n4 Scorpio dates: October 23 – November 21\n\n1 Sagittarius dates: November 22 – December 21\n2 Capricorn dates: December 22 – January 19\n3 Aquarius dates: January 20 – February 18\n4 Pisces dates:February 19 – March 20\n\nAnton Szandor LaVey\n  born 1930-04-11\n  calendar astrology:\n    aries = cardinal fire\n    but LaVey is water, and only is playing fire = 4p1\n\nGneedl\n  born 2017-03-13\n  calendar astrology:\n    Pisces = mutable water\n    but Gneedl is fire\n    also was forced to birth 5 weeks too early\n    birth was predicted around 2017-04-17 = aries\n    will be forced to "play water"\n\nmila\n  born 1989-08-23\n  calendar astrology:\n    virgo = mutable earth\n    but mila is fire\n    parents said Leo = fixed fire\n    earth is "impossible to play" for fire\n\n\n\n*/\n\nexport const zodiacMutableFromSM = [\n  [\'gemini\', \'Gemini = mutable air\'],\n  [\'sagittarius\', \'Sagittarius = mutable fire\'],\n  [\'virgo\', \'Virgo = mutable earth\'],\n  [\'pisces\', \'Pisces = mutable water\'],\n]\n\nexport const zodiacCardinalFromSM = [\n  [\'libra\', \'Libra = cardinal air\'],\n  [\'aries\', \'Aries = cardinal fire\'],\n  [\'capricorn\', \'Capricorn = cardinal earth\'],\n  [\'cancer\', \'Cancer = cardinal water\'],\n]\n\nexport const zodiacFixedFromSM = [\n  [\'aquarius\', \'Aquarius = fixed air\'],\n  [\'leo\', \'Leo = fixed fire\'],\n  [\'taurus\', \'Taurus = fixed earth\'],\n  [\'scorpius\', \'Scorpius = fixed water\'],\n]\n\n\n\n// names and translations\n// key path = locale, scope, key\n\n// names and translations\n// key path = locale, scope, key\nconst nameByLocaleData = {\n\n  en: {\n    asmg: {\n      "A": "Son",\n      "B": "Mother",\n      "C": "Daughter",\n      "D": "Father",\n\n      "1": "Fire", //  = son inside\n      "2": "Earth", //  = mother inside\n      "3": "Air", //  = daughter inside\n      "4": "Water", //  = father inside\n\n      "M": "Male",\n      "F": "Female",\n      "S": "Small", // TODO young\n      "L": "Large", // TODO old\n\n      "P": "Psychotic", //  = male inside\n      "N": "Neurotic", //  = female inside\n      "E": "Extravert", //  = young inside\n      "I": "Introvert", //  = old inside\n    },\n\n    mbti: {\n      // first half = sense + move\n      "N": "Ntuit", //  = extravert sense = young inside\n      "S": "Sense", //  = introvert sense = old inside\n      "E": "\'Extravert\'", //  = neurotic move = female inside\n      "I": "\'Introvert\'", //  = psychotic move = male inside\n\n      // second half = (sense + move) of pathos\n      "P": "Perceive", //  = extravert sense = young inside\n      "J": "Judge", //  = introvert sense = old inside\n      "F": "Feel", //  = neurotic move = female inside\n      "T": "Think", //  = psychotic move = male inside\n      // note. four combinations are impossible:\n      // INFJ, ESTP, ENTJ, ISFP\n      // cos you never can play your opposite type\n      // fire can never play earth, and vice versa\n      // air can never play water, and vice versa\n      // these impossible results serve\n      // as control parameter for the MBTI test\n      // so yes, MBTI is "only astrology with extra steps" :P\n\n      // 16 names\n\n      // first/left half = body, center, "nomos", name, self\n      // second/right half = pathos\n\n      // IN = 1 = TP\n      // ES = 2 = FJ\n      // EN = 3 = FP\n      // IS = 4 = TJ\n\n      "INTP": "Designer, Theorizer, Architect", // 1p1\n      "INFJ": "Foreseer, Developer, Counselor", // 1p2 [forbidden in alchi]\n      "INTJ": "Conceptualizer, Director, Mastermind", // 1p4\n      "INFP": "Harmonizer, Clarifier, Healer", // 1p3\n\n      "ESFJ": "Facilitator, Caretaker, Provider", // 2p2\n      "ESFP": "Motivator, Presenter, Performer", // 2p3\n      "ESTJ": "Implementor, Supervisor", // 2p4\n      "ESTP": "